:root {
  --primary-text-color: #fff;
  --black-text-color: #000;
  --accent-text-color: #ff6b08;
  --footer-text-color: #545454;
  --hero-background: rgba(0, 0, 0, .56);
  --primary-background: #fff;
  --footer-background: #f7f7f7;
  --font-family: "Roboto", sans-serif;
  --color-input: rgba(255, 255, 255, .7);
}

body {
  background-color: #000;
}

.transform {
  cursor: pointer;
}

.form__group {
  flex-basis: 100%;
  position: relative;
}

.container-input {
  justify-content: center;
  padding-top: 50px;
  display: flex;
}

.input-position {
  width: 280px;
  border: none;
  border-bottom: 1px solid var(--color-input);
  cursor: pointer;
  color: var(--primary-text-color);
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .input-position {
    width: 336px;
  }
}

@media screen and (min-width: 1280px) {
  .input-position {
    width: 394px;
  }
}

.film-input {
  color: var(--color-input);
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.input-position:focus + .film-input, .input-position:not(:placeholder-shown) + .film-input {
  color: var(--primary-text-color);
  transform-origin: 0 0;
  transform: translateY(-30px);
}

.form__group:hover > .input-position, .form__group:hover > .film-input {
  color: var(--primary-text-color);
  border-color: var(--primary-text-color);
}

.button-input {
  color: var(--primary-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.svg-link {
  color: var(--primary-text-color);
}

.find {
  fill: currentColor;
  justify-content: center;
  align-items: center;
  display: flex;
}

